<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  methods: {
    sendMessage (cmd, params) {
      window.parent.postMessage({ cmd, params }, '*')
    },
    handleMessage (msg) {
      const { data } = msg
      if (data.cmd === 'changeMonth') {
        const company = Object.assign(this.$store.state.company[0], {
          time_start: data.params + '-01',
          time_end: data.params + '-31',
          shenbao_month: data.params
        })
        this.$store.commit('addcompany', company)
      }
      if (data.cmd === 'preset') { // 预设的企业信息
        const company = {
          taxpayerNumber: data.params['tax_id'],
          address: data.params['register_address'],
          yawage: '40000.00元', // 年平均工资
          time_start: data.params['month'] + '-01',
          mawage: '3000.00元', // 月平均工资
          time_end: data.params['month'] + '-31',
          shenbao_month: data.params['month'],
          lanch: '10000.00元', // 月公积金减除上线
          personName: data.params['legal_person'],
          personTel: data.params['legal_person_tel'],
          fixTel: '1688888888', // 固定电话
          empoly: '人事',
          isSelect: false,
          name: data.params['company_title'],
          isShenbao: '未申报',
          remark: '备注'
        }
        this.$store.commit('addcompany', company)
        let monthNumber = parseInt(company.shenbao_month.substring(5, 7)) // 获取月份
        this.$store.commit('setMonthNumber', monthNumber)
      }
      if (data.cmd === 'getAnswer') {
        const answer = Object.assign(this.$store.state.company.answer, this.$store.state.form.answer)
        let keys = Object.keys(answer)
        if (keys.length > 0) {
          this.sendMessage('echo', { business_id: data.params['business_id'], answer: answer })
          this.$store.commit('resetAnswer')
        }
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      console.log('个税系统注册消息成功')
      window.addEventListener('message', this.handleMessage)
      // 模拟预设数据
      // const company = {
      //   taxpayerNumber: '91370305755666003U',
      //   address: '山东省淄博市',
      //   yawage: '40000.00元', // 年平均工资
      //   time_start: '2020-09-01',
      //   mawage: '3000.00元', // 月平均工资
      //   time_end: '2020-09-31',
      //   shenbao_month: '2020-09',
      //   lanch: '10000.00元', // 月公积金减除上线
      //   personName: '法人',
      //   personTel: '370303199201160323',
      //   fixTel: '1688888888', // 固定电话
      //   empoly: '人事',
      //   isSelect: false,
      //   name: '问云机械',
      //   isShenbao: '未申报',
      //   remark: '备注'
      // }
      // this.$store.commit('addcompany', company)
      // let monthNumber = parseInt(company.shenbao_month.substring(5, 7)) // 获取月份
      // this.$store.commit('setMonthNumber', monthNumber)
    })
  },
  destroyed () {
    window.removeEventListener('message', this.handleMessage)
  }
}
</script>

<style>
  * {
    margin: 0;
    padding: 0;
  }
  html,body {
    width: 100%;
    height: 100%;
  }
  #app {
    /* font-family: 'Avenir', Helvetica, Arial, sans-serif; */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    width: 100%;
    height: 100%;
  }

  a {
    text-decoration: none;
  }

  .collection {
    min-width: 800px;
    position: relative;
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
  }

  input[type="number"] {
    -moz-appearance: textfield;
  }

</style>



