// 公司的所有信息
module.exports = {
  state: {
    answer: {},
    company: []
  },
  getters: {
    getcompanyInfo: (state) => (taxpayerNumber) => {
      for (let i in state) {
        if (state[i].taxpayerNumber === taxpayerNumber) {
          let data = Object.create(state[i])
          data.number = i
          return data
        }
      }
    }
  },
  mutations: {
    resetAnswer (state) {
      state.answer = {}
    },
    addcompany: (state, data) => { // 添加企业
      state.company.push(data)
      state.answer['company_' + data['taxpayerNumber']] = data
    },
    updateOneNumbercompany (state, data) { // 更新一个企业的一条信息
      state[data.keyNumber][data.key] = data.value
    },
    upAllcompanyData (state, data) { // 更新企业的表
      state[this.getters.getcompanyInfo(this.state.companyNow.taxpayerNumber).number].data = data // 更新企业内容
      let mainData = {}
      mainData.serviceId = this.state.service.servicesId
      mainData.content = state
    },
    updateOneCompany (state, data) {
      state[this.getters.getcompanyInfo(this.state.companyNow.taxpayerNumber).number].data[data.infoid] = data.data
    }
  },
  action: {}
}



