// 企业和当前业务
module.exports = {
  state: {
    status: '', // 用于检测状态，是实训还是账套还是老师
    businessCount: 0, // 当前业务的key
    trade_id: '', // 公司的Id
    monthNumber: 0, // 月份的数字,
    role: '' // 用于检测是学生还是老师
  },
  mutations: {
    setPersonStatus (state, data) { // 改变人员状态
      this.state.service.status = data
    },
    setCompanyId (state, data) { // 改变公司id
      this.state.service.trade_id = data
    },
    setMonthNumber (state, data) {
      this.state.service.monthNumber = data
    },
    businessCount (state, number) {
      if (state.businessCount >= this.state.business.length - 1 && number === 1) {
        alert('最后一个咯')
      } else if (state.businessCount === 0 && number === -1) {
        alert('最开始一个咯')
      } else {
        this.state.service.businessCount += number
        this.state.service.servicesId = this.state.business[state.businessCount]._id
      }
    }
  }
}



