// 用于缓存配偶的信息的
module.exports = {
  state: [],
  getters: {
    getMate: state => personInfoId => {
      if (state.length >= 1) {
        for (let i in state) {
          if (state[i].personInfoId === personInfoId) {
            return state[i]
          }
        }
        return false
      }
      return false
    }
  },
  mutations: {
    addMeta (state, data) {
      let add = true
      if (state.length >= 1) {
        for (let i in state) {
          if (state[i].personInfoId === data.personInfoId) {
            state[i] = data
            add = false
            break
          }
        }
        if (add) {
          state.push(data)
        }
      } else {
        state.push(data)
      }
    }
  },
  action: {}
}



