import Vue from 'vue'
import Vuex from 'vuex'
import service from './moudles/service'
import form from './moudles/form'
import company from './moudles/company'
import mate from './moudles/mate'
// import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)
const store = new Vuex.Store({
  strict: true,
  state: {
  },
  getters: {
    company () {
      return company.state.company
    }
  },
  modules: {
    service,
    form,
    company,
    mate
  }
  // plugins: [createPersistedState({
  //   key: 'geshui',
  //   storage: window.sessionStorage
  // })]
})

export default store



