// 所有的表单
module.exports = {
  state: {
    answer: {},
    personCollection: [], // 人员信息
    payChildForm: [], // 子女教育支出
    payContinueForm: [], // 继续教育支出
    payInterestForm: [], // 住房利息支出
    payRentForm: [], // 住房租金支出
    payOldmanForm: [], // 赡养老人支出
    normalForm: [], // 正常薪金所得
    yearForm: [], // 全年一次性奖金收入
    laborForm: [], // 劳务报酬所得
    shenbaoForm: [], // 申报的表单
    shenbaoLog: [], // 所有次数申报记录
    jiaokuanForm: [], // 缴款了的数据
    allPersonLog: [], // 所有人的申报记录
    infoId: 0 // 作为数据的主键使用
  },
  getters: {
    getFormContent: (state) => (formId) => { // 获取整个表数据的信息
      // this.state.form[formId] = this.state.form[formId] || []
      // return state[formId] ? state[formId] : []
      return state[formId]
    },
    getFormOneInfo: (state) => (formId, infoId) => { // 获取表中的一条信息
      for (let i in state[formId]) {
        if (state[formId][i].infoId === infoId) {
          return state[formId][i]
        }
      }
    },
    getFormInfoId: (state) => (key) => { // 获取主键的info
      return state[key].length + 1
    },
    getAllPersonTax: (state, getters) => {
      return [...state.normalForm ? state.normalForm : [], ...state.yearForm ? state.yearForm : [], ...state.laborForm ? state.laborForm : []]
    }
  },
  mutations: {
    resetAnswer (state) {
      state.answer = {}
    },
    addFormInfo (state, data) { // 添加一条信息   formId 表的主键 content 表的内容 infoId 信息的主键(数组) isBaosong 表的报送状态
      this.state.form[[data.formId]] = this.state.form[[data.formId]] || []
      if (data.key) {
        let isSame = false
        for (let i in state[data.formId]) {
          if (state[data.formId][i][data.key] === data.content[data.key]) {
            isSame = true
            alert('身份证号重复')
            return false
          }
        }
        if (!isSame) {
          state[data.formId].push(data.content)
          // 记录答案
          state.answer[data.formId + '|' + data.content['cardNumber']] = data.content
        }
      } else {
        state[data.formId].push(data.content)
        // 记录答案
        state.answer[data.formId + '|' + data.content['cardNumber']] = data.content
      }
    },
    deleteFormInfo (state, data) { // 删除一条信息 data.infoId为数组，要删除的信息的key
      for (let i in data.infoId) {
        for (let j = 0; j < state[data.formId].length; j++) {
          if (state[data.formId][j].infoId === data.infoId[i]) {
            let res = state[data.formId].splice(j, 1)
            // 记录答案
            delete state.answer[data.formId + '|' + res[0]['cardNumber']]
            break
          }
        }
      }
    },
    updataFormInfo (state, data) { // 更新表格的数据 content为全表内容 infoId为唯一表识
      for (let i in state[data.formId]) {
        if (state[data.formId][i].infoId === data.content.infoId) {
          // 记录答案
          const number = state[data.formId][i].cardNumber || state[data.formId][i].mateCardNumber
          delete state.answer[data.formId + '|' + number]
          state[data.formId][i] = data.content

          // 记录答案
          state.answer[data.formId + '|' + data.content['cardNumber']] = data.content
          break
        }
      }
    },
    updataBaosong (state, data) { // 报送多少条数据的操作
      for (let i in data.infoId) {
        for (let j = 0; j < state[data.formId].length; j++) {
          if (state[data.formId][j].infoId === data.infoId[i]) {
            state[data.formId][j].isBaosong = '报送中'
            // 记录答案
            state.answer[data.formId + '|' + state[data.formId][j]['cardNumber']]['isBaosong'] = '报送中'
            break
          }
        }
      }
    },
    updataFankui (state, data) { // 反馈多少条数据的操作
      for (let i in data.infoId) {
        for (let j = 0; j < state[data.formId].length; j++) {
          if (state[data.formId][j].infoId === data.infoId[i]) {
            if (state[data.formId][j].isBaosong === '报送中') {
              state[data.formId][j].isBaosong = '已报送'
              state[data.formId][j].isShenfen = '已验证'
              break
            }
          }
        }
      }
    },
    upFormAll (state, data) { // 更新所有的表单，用于切换公司
      this.state.form = data
    },
    upShenbaoForm (state, all) { // 更新申报的表单
      let data = {}
      data.personNumber = 0
      for (let i in state.normalForm) {
        state.shenbaoForm.unshift(state.normalForm[i])
        state.allPersonLog.unshift(state.normalForm[i])
      }
      for (let i in state.yearForm) {
        state.shenbaoForm.unshift(state.yearForm[i])
        state.allPersonLog.unshift(state.yearForm[i])
      }
      for (let i in state.laborForm) {
        state.shenbaoForm.unshift(state.laborForm[i])
        state.allPersonLog.unshift(state.laborForm[i])
      }
      state.shenbaoLog.unshift(all)
      state.normalForm = []
      state.yearForm = []
      state.laborForm = []

      // 设置答案
      state.answer.shenbaoForm = state.shenbaoForm
      state.answer.allPersonLog = state.allPersonLog
    },
    upJiaokuan (state) { // 更新缴款
      let data = {}
      data.shenbaobiao = '扣缴所得税报告表'
      data.tuibu = 0
      for (let i in state.shenbaoForm) {
        data.tuibu += state.shenbaoForm[i].tuibu
      }
      data.shijiao = data.tuibu
      data.status = '已缴款'
      state.jiaokuanForm.unshift(data)
      state.shenbaoForm = []
      // 设置答案
      state.answer.jiaokuanForm = state.jiaokuanForm
    },
    upAll (state, data) { // 更新所有数据，用于答案还原
      this.state.form = data
    }
  },
  action: {}
}



