import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
export default new Router({
  routes: [{
    path: '/404',
    name: '404',
    component: resolve => require(['@/pages/404'], resolve)
  },
  {
    path: '/index',
    component: resolve => require(['@/pages/index'], resolve),
    children: [
      {
        path: '/index',
        name: 'index',
        component: resolve => require(['@/pages/index/index'], resolve)
      },
      {
        path: '/collection',
        name: 'collection',
        component: resolve => require(['@/pages/collection'], resolve)
      },
      {
        path: '/payCollectionChild',
        name: 'payCollectionChild',
        component: resolve => require(['@/pages/pay-collection/child'], resolve)
      },
      {
        path: '/payEducationContinue',
        name: 'payEducationContinue',
        component: resolve => require(['@/pages/pay-collection/continue'], resolve)
      },
      {
        path: '/payHouseInterest',
        name: 'payHouseInterest',
        component: resolve => require(['@/pages/pay-collection/house-interest'], resolve)
      },
      {
        path: '/payHouseRent',
        name: 'payHouseRent',
        component: resolve => require(['@/pages/pay-collection/house-rent'], resolve)
      },
      {
        path: '/payOldman',
        name: 'payOldman',
        component: resolve => require(['@/pages/pay-collection/oldman'], resolve)
      },
      {
        path: '/selectCompany',
        name: 'selectCompany',
        component: resolve => require(['@/pages/select-statist/company'], resolve)
      },
      {
        path: '/selectPerson',
        name: 'selectPerson',
        component: resolve => require(['@/pages/select-statist/person'], resolve)
      },
      {
        path: '/taxHistory',
        name: 'taxHistory',
        component: resolve => require(['@/pages/tax-payment/history-select'], resolve)
      },
      {
        path: '/taxThree',
        name: 'taxThree',
        component: resolve => require(['@/pages/tax-payment/three-payment'], resolve)
      },
      {
        path: '/declaration',
        name: 'declaration',
        redirect: '/declaration/allDeclaration',
        component: resolve => require(['@/pages/declaration/index'], resolve),
        children: [
          {
            path: '/declaration/taxCalculation',
            name: 'taxCalculation',
            component: resolve => require(['@/pages/declaration/components/taxCalculation'], resolve)
          },
          {
            path: '/declaration/allDeclaration',
            name: 'allDeclaration',
            component: resolve => require(['@/pages/declaration/components/all-declaration'], resolve)
          },
          {
            path: '/declaration/extraTable',
            name: 'extraTable',
            component: resolve => require(['@/pages/declaration/components/extra-table'], resolve)
          },
          {
            path: '/declaration/application',
            name: 'application',
            component: resolve => require(['@/pages/declaration/components/application'], resolve)
          }
        ]
      },
      {
        path: '/normalSalary',
        name: 'normalSalary',
        component: resolve => require(['@/pages/declaration-table/normal-salary'], resolve)
      },
      {
        path: '/laborSalary',
        name: 'laborSalary',
        component: resolve => require(['@/pages/declaration-table/labor-salary'], resolve)
      },
      {
        path: '/yearSalary',
        name: 'yearSalary',
        component: resolve => require(['@/pages/declaration-table/year-salary'], resolve)
      },
      {
        path: '/Page3',
        name: 'Page3',
        component: resolve => require(['@/pages/components/page-content'], resolve)
      }
    ]
  },
  {
    path: '/admin',
    name: 'admin',
    redirect: '/admin/business',
    component: resolve => require(['@/personTaxAdmin/index'], resolve),
    children: [
      {
        path: '/admin/business',
        name: 'business',
        component: resolve => require(['@/personTaxAdmin/business/index'], resolve)
      }
    ]
  },
  {
    path: '/',
    name: 'rua',
    redirect: '/login'
  },
  {
    path: '/login',
    name: 'login',
    redirect: '/login/normal:company_id?',
    component: resolve => require(['@/pages/login/index'], resolve),
    children: [
      {
        path: '/login/answer/:company_id?',
        name: 'answerLogin',
        component: resolve => require(['@/pages/login/index'], resolve)
      },
      {
        path: '/login/normal:company_id?',
        name: 'nomarl',
        component: resolve => require(['@/pages/login/index'], resolve)
      },
      {
        path: '/login/parse/:company_id?',
        name: 'parse',
        component: resolve => require(['@/pages/login/index'], resolve)
      },
      {
        path: '/login/account/:company_id?',
        name: 'account',
        component: resolve => require(['@/pages/login/index'], resolve)
      }
    ]
  },
  {
    path: '*',
    component: resolve => require(['@/pages/404'], resolve)
  }
  ]
})



